import axios from 'axios';

// –ë–∞–∑–æ–≤–∞—è –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è axios
const api = axios.create({
  baseURL: import.meta.env.VITE_API_URL || 'http://localhost:5000',
  timeout: 10000,
});

export interface AdminStats {
  activeStudents: number;
  instructors: number;
  vehicles: number;
  documents: number;
  totalStudents: number;
  totalCourses: number;
  totalRevenue: number;
  completionRate: number;
}

export interface IApiResponse<T> {
  status: 'success' | 'error';
  data?: T;
  message?: string;
}

export const adminStatsApi = {
  // –ü–æ–ª—É—á–∏—Ç—å –±—ã—Å—Ç—Ä—É—é —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –¥–ª—è –∞–¥–º–∏–Ω –ø–∞–Ω–µ–ª–∏
  getQuickStats: async (): Promise<AdminStats> => {
    try {
      const response = await api.get<IApiResponse<AdminStats>>('/admin/stats/quick');
      
      if (response.data.status === 'success' && response.data.data) {
        return response.data.data;
      }
      
      throw new Error(response.data.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏');
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ API adminStats.getQuickStats:', error);
      // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –¥–µ—Ñ–æ–ª—Ç–Ω—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
      return {
        activeStudents: 0,
        instructors: 0,
        vehicles: 0,
        documents: 0,
        totalStudents: 0,
        totalCourses: 0,
        totalRevenue: 0,
        completionRate: 0
      };
    }
  },

  // –ü–æ–ª—É—á–∏—Ç—å –¥–µ—Ç–∞–ª—å–Ω—É—é —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É
  getDetailedStats: async (): Promise<any> => {
    try {
      const response = await api.get<IApiResponse<any>>('/admin/stats/detailed');
      
      if (response.data.status === 'success' && response.data.data) {
        return response.data.data;
      }
      
      throw new Error(response.data.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–µ—Ç–∞–ª—å–Ω–æ–π —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏');
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ API adminStats.getDetailedStats:', error);
      return {};
    }
  },

  // –ü–æ–ª—É—á–∏—Ç—å —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –ø–æ –º–µ—Å—è—Ü–∞–º
  getMonthlyStats: async (): Promise<any> => {
    try {
      const response = await api.get<IApiResponse<any>>('/admin/stats/monthly');
      
      if (response.data.status === 'success' && response.data.data) {
        return response.data.data;
      }
      
      throw new Error(response.data.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –º–µ—Å—è—á–Ω–æ–π —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏');
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ API adminStats.getMonthlyStats:', error);
      return {};
    }
  }
};

export default adminStatsApi; 